// app/camera/[cameraName]/page.tsx
import { useParams } from 'next/navigation'; // Thay vì dùng useRouter, sử dụng useParams

const CameraPage = () => {
  const { cameraName } = useParams(); // Sử dụng useParams để lấy các tham số từ URL

  return (
    <div>
      <h1>Trang camera: {cameraName}</h1>
    </div>
  );
};

export default CameraPage;
